*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

input,
button,
textarea {
  padding: 0;
  background: none;
  border: none;
  outline: none;
  color: inherit;
}

html,
body {
  height: 100%;
  padding: 0;
  scroll-behavior: smooth;
  scrollbar-gutter: stable;
}

body {
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

p {
  text-wrap: pretty;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  text-wrap: balance;
}

ul,
ol {
  list-style-type: none;
  padding: 0;
}

a {
  text-decoration: none;
}

.main-wrapper-outer {
  width: 100%;
  min-height: 100%;
  max-width: var(--main-outer-width);
  margin-left: auto;
  margin-right: auto;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: stretch;
  overflow: hidden;
}

.main {
  flex-grow: 1;
  flex-shrink: 1;
}

.container {
  margin: 0 auto;
  width: 100%;
  max-width: var(--container-width);
  padding: 0px 20px;
}

.noUi-target,
.noUi-target * {
  -webkit-touch-callout: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-user-select: none;
  -ms-touch-action: none;
  touch-action: none;
  -ms-user-select: none;
  -moz-user-select: none;
  user-select: none;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

.noUi-target {
  position: relative;
}

.noUi-base,
.noUi-connects {
  width: 100%;
  height: 100%;
  position: relative;
  z-index: 1;
}

/* Wrapper for all connect elements.
 */
.noUi-connects {
  overflow: hidden;
  z-index: 0;
}

.noUi-connect,
.noUi-origin {
  will-change: transform;
  position: absolute;
  z-index: 1;
  top: 0;
  right: 0;
  height: 100%;
  width: 100%;
  -ms-transform-origin: 0 0;
  -webkit-transform-origin: 0 0;
  -webkit-transform-style: preserve-3d;
  transform-origin: 0 0;
  transform-style: flat;
}

/* Offset direction
 */
.noUi-txt-dir-rtl.noUi-horizontal .noUi-origin {
  left: 0;
  right: auto;
}

/* Give origins 0 height/width so they don't interfere with clicking the
 * connect elements.
 */
.noUi-vertical .noUi-origin {
  top: -100%;
  width: 0;
}

.noUi-horizontal .noUi-origin {
  height: 0;
}

.noUi-handle {
  backface-visibility: hidden;
  position: absolute;
}

.noUi-touch-area {
  height: 100%;
  width: 100%;
}

.noUi-state-tap .noUi-connect,
.noUi-state-tap .noUi-origin {
  transition: transform 0.3s;
}

.noUi-state-drag * {
  cursor: inherit !important;
}

/* Slider size and handle placement;
 */
.noUi-horizontal {
  height: 3px;
}

.noUi-horizontal .noUi-handle {
  width: 15px;
  height: 15px;
  right: -14px;
  top: -6px;
  border-radius: 50%;
  background-color: #40b661;
}
.noUi-horizontal .noUi-handle::after {
  content: "";
  position: absolute;
  background-color: #fff;
  border-radius: 50%;
  width: 6px;
  height: 6px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.noUi-base {
  width: calc(100% - 5px);
}

.noUi-vertical {
  width: 18px;
}

.noUi-vertical .noUi-handle {
  width: 28px;
  height: 34px;
  right: -6px;
  bottom: -17px;
}

.noUi-txt-dir-rtl.noUi-horizontal .noUi-handle {
  left: -17px;
  right: auto;
}

/* Styling;
 * Giving the connect element a border radius causes issues with using transform: scale
 */
.noUi-target {
  background: #fff;
  border-radius: 40px;
}

.noUi-connect {
  position: relative;
  height: 3px;
  border-radius: 40px;
  background: #40b661;
}

/* Handles and cursors;
 */
.noUi-draggable {
  cursor: ew-resize;
}

.noUi-vertical .noUi-draggable {
  cursor: ns-resize;
}

.noUi-handle {
  cursor: pointer;
}

.noUi-active {
  background: #2f3278;
  border: 1px solid #2f3278;
}

/* Handle stripes;
 */
/* Disabled state;
 */
[disabled] .noUi-connect {
  background: #b8b8b8;
}

[disabled].noUi-target,
[disabled].noUi-handle,
[disabled] .noUi-handle {
  cursor: not-allowed;
}

/* Base;
 *
 */
.noUi-pips,
.noUi-pips * {
  box-sizing: border-box;
}

.noUi-pips {
  position: absolute;
  color: #999;
}

/* Values;
 *
 */
.noUi-value {
  position: absolute;
  white-space: nowrap;
  text-align: center;
  font-weight: 400;
  font-size: 16px;
  color: #686868;
}

.noUi-value-sub {
  color: #ccc;
  font-size: 10px;
}

/* Markings;
 *
 */
.noUi-marker {
  position: absolute;
  background: #ccc;
}

.noUi-marker-sub {
  background: #aaa;
}

.noUi-marker-large {
  background: #aaa;
}

/* Horizontal layout;
 *
 */
.noUi-pips-horizontal {
  padding: 10px 0px 0px 0px;
  height: 20px;
  top: 100%;
  left: 5px;
  width: calc(100% - 20px);
}

.noUi-value-horizontal {
  transform: translate(-50%, 50%);
}

.noUi-rtl .noUi-value-horizontal {
  transform: translate(50%, 50%);
}

.noUi-marker-horizontal.noUi-marker {
  display: none;
  margin-left: -1px;
  width: 2px;
  height: 5px;
}

.noUi-marker-horizontal.noUi-marker-sub {
  height: 10px;
}

.noUi-marker-horizontal.noUi-marker-large {
  display: none;
  height: 15px;
}

/* Vertical layout;
 *
 */
.noUi-pips-vertical {
  padding: 0 10px;
  height: 100%;
  top: 0;
  left: 100%;
}

.noUi-value-vertical {
  transform: translate(0, -50%);
  padding-left: 25px;
}

.noUi-rtl .noUi-value-vertical {
  transform: translate(0, 50%);
}

.noUi-marker-vertical.noUi-marker {
  width: 5px;
  height: 2px;
  margin-top: -1px;
}

.noUi-marker-vertical.noUi-marker-sub {
  width: 10px;
}

.noUi-marker-vertical.noUi-marker-large {
  width: 15px;
}

.noUi-tooltip {
  display: block;
  position: absolute;
  border: 1px solid #d9d9d9;
  border-radius: 3px;
  background: #fff;
  color: #000;
  padding: 5px;
  text-align: center;
  white-space: nowrap;
}

.noUi-horizontal .noUi-tooltip {
  transform: translate(-50%, 0);
  left: 50%;
  bottom: 120%;
}

.noUi-vertical .noUi-tooltip {
  transform: translate(0, -50%);
  top: 50%;
  right: 120%;
}

.noUi-horizontal .noUi-origin > .noUi-tooltip {
  transform: translate(50%, 0);
  left: auto;
  bottom: 10px;
}

.noUi-vertical .noUi-origin > .noUi-tooltip {
  transform: translate(0, -18px);
  top: auto;
  right: 28px;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.form__inputs {
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.form__input {
  position: relative;
  border-radius: 10px;
  background: rgba(237, 237, 237, 0.6);
  border: 1px solid transparent;
  height: 52px;
  transition: border 0.3s;
  padding: 9px 16px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.form__input.select-input {
  cursor: pointer;
  padding: 9px 36px 9px 16px;
}
.form__input.select-input._form-focus {
  border: 1px solid transparent;
}
.form__input.select-input._form-focus .form__placeholder {
  display: none;
}
.form__input.select-input img {
  position: absolute;
  right: 18px;
  top: 50%;
  transform: translate(0px, -50%);
  width: 8px;
  height: 13px;
}
.form__input.select-input input {
  pointer-events: none;
  white-space: nowrap; /*    */
  overflow: hidden; /*   */
  text-overflow: ellipsis;
}
.form__input._form-focus {
  border: 1px solid var(--theme-color-accent);
}
.form__input._form-focus .form__placeholder {
  display: block;
}
.form__input input {
  width: 100%;
  font-weight: 500;
  font-size: 16px;
  line-height: 150%;
  color: #333;
}
.form__input input::placeholder {
  color: #333;
}
.form__placeholder {
  display: none;
  font-weight: 400;
  font-size: 12px;
  line-height: 120%;
  color: #8e8e93;
}
.form__text {
  font-weight: 300;
  font-size: 12px;
  line-height: 133%;
  text-align: center;
  color: #8e8e93;
}
.form__text a {
  text-decoration: underline;
  text-decoration-skip-ink: none;
  color: #5133ed;
  transition: opacity 0.3s;
}
.form__text a:hover {
  opacity: 0.7;
}

.select {
  min-width: 0;
  position: relative;
}
.select__body {
  position: relative;
}
.select__title {
  color: inherit;
  text-align: left;
  border-radius: 10px;
  background-color: #fafafa;
  border: 1px solid #e2e2e2;
  transition: border-radius 0.3s;
  cursor: pointer;
  width: 100%;
  font-weight: 500;
  font-size: 16px;
  padding: 45px 65px 18px 30px;
  display: flex;
  align-items: center;
  gap: 6px;
  height: 74px;
  position: relative;
}
@media (max-width: 768px) {
  .select__title {
    padding: 45px 40px 18px 18px;
    background-color: #fff;
  }
}
.select__title::after {
  content: "";
  position: absolute;
  right: 44px;
  top: 42%;
  transition: transform 0.3s;
  width: 8px;
  height: 13px;
  display: block;
  transform: rotate(90deg);
  background-size: 100% 100%;
  background-image: url("../img/icon/arrow1.svg");
  transition: filter 0.3s;
}
@media (max-width: 768px) {
  .select__title::after {
    right: 18px;
  }
}
._select-open .select__title {
  border-radius: 10px 10px 0px 0px;
}
._select-open .select__title::after {
  content: "";
  transform: rotate(-90deg);
}
.select__value {
  display: flex;
  min-width: 0;
  overflow: hidden;
  align-items: center;
  flex: 1 1 auto;
}
.select__row {
  display: flex;
  align-items: center;
}
.select__content {
  flex: 1 1 auto;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  display: inline-block;
}
.select__text {
  flex: 1 1 auto;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.select__options {
  position: absolute;
  top: 100%;
  border-radius: 0px 0px 10px 10px;
  min-width: 100%;
  left: 0;
  border-left: 1px solid #e2e2e2;
  border-right: 1px solid #e2e2e2;
  border-bottom: 1px solid #e2e2e2;
  background-color: #fafafa;
  padding: 10px 0px 5px 0px;
  max-height: 115px;
  overflow: auto;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.select__options::-webkit-scrollbar {
  display: none;
}
@media (max-width: 768px) {
  .select__options {
    background-color: #fff;
  }
}
.select__option {
  width: 100%;
  text-align: left;
  cursor: pointer;
  padding: 4px 30px;
  color: inherit;
  font-weight: 500;
  font-size: 16px;
  line-height: 133%;
  transition: color 0.3s;
}
.select__option:hover {
  color: var(--theme-color-accent);
}
@media (max-width: 768px) {
  .select__option {
    padding: 4px 18px;
  }
}

.salon-options {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin: 0px 32px 0px 0px;
}
@media (max-width: 600px) {
  .salon-options {
    margin: 0px 20px 0px 0px;
  }
}
.salon-options__item {
