    border-radius: 0px;
  }
}
.salon-options__top {
  display: flex;
  justify-content: space-between;
  align-items: start;
  gap: 20px;
}
.salon-options__title {
  flex: 0 1 236px;
  font-weight: 600;
  font-size: 20px;
  line-height: 125%;
}
.salon-options__icon {
  margin: 5px 0px 0px 0px;
