    font-size: 13px;
  }
}
.salon-options__column img {
  width: 24px;
  height: 24px;
  flex: 0 0 24px;
}
.salon-options__column p {
  font-weight: 400;
  font-size: 16px;
  line-height: 120%;
}
.salon-options__column p.grey {
  color: #a4a2a3;
}
@media (max-width: 600px) {
  .salon-options__column p {
    font-size: 14px;
  }
}
.salon-options__map {
  width: 40px;
  height: 40px;
  border-radius: 10px;
  flex: 0 0 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--theme-color-text-common);
  transition: opacity 0.3s;
}
.salon-options__map img {
  width: 24px;
  height: 24px;
}
.salon-options__map:hover {
  opacity: 0.7;
}

