  top: 0;
  left: 0;
  width: 100%;
  height: auto;
  z-index: 100;
  transition: background-color 0.3s, padding-top 0.3s, padding-bottom 0.3s;
  background-color: transparent;
  color: #fff;
  padding-top: 30px;
  padding-bottom: 30px;
}
