    padding-top: 63px;
    padding-bottom: 63px;
  }
}
.header._header-scroll, .menu-open .header {
  background-color: #fff;
  color: var(--theme-color-text-common);
}
@media (max-width: 768px) {
  .header._header-scroll, .menu-open .header {
    padding-top: 10px;
    padding-bottom: 10px;
  }
}
