.icon-menu {
  position: relative;
  width: 18px;
  height: 12px;
  cursor: pointer;
  display: block;
  z-index: 5;
}
.icon-menu span, .icon-menu::before, .icon-menu::after {
  content: "";
  left: 0;
  position: absolute;
  width: 100%;
  height: 2px;
  border-radius: 15px;
  background-color: #fff;
  transition: background-color 0.3s;
}
.icon-menu::before {
  top: 0;
}
.icon-menu::after {
  bottom: 0;
}
.icon-menu span {
  top: calc(50% - 1px);
}
.menu-open .icon-menu::before {
  top: calc(50% - 1px);
  transform: rotate(-45deg);
}
.menu-open .icon-menu span {
  width: 0;
}
.menu-open .icon-menu::after {
  content: "";
  width: 100%;
  bottom: calc(50% - 1px);
  transform: rotate(45deg);
}

